.word {
  display: inline-block;
  position: relative;
  transition: transform 700ms ease-in-out;
}
.correct {
  color: #6aa84f;
  font-weight: 400;
}
.incorrect {
  color: #797270;
  font-weight: 400;
}
.incorrectUnderline {
  text-decoration-line: underline;
  text-decoration-style: dotted;
  text-decoration-color: purple;
  text-decoration-thickness: 2px;
}
.flashOnIncorrect {
  animation: incorrectHighlight 300ms linear both,
    shake 700ms cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
}
@keyframes incorrectHighlight {
  100.0% {
    color: #ffa8a9;
    font-weight: 800;
  }
}
@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}
.wordSelected {
  text-decoration: underline solid #453f3c;
  -webkit-text-decoration: underline solid #453f3c;
  color: #453f3c;
}
.wordSwap {
  transform: translate(var(--transLeft), var(--transTop));
}
.nowrap {
  white-space: nowrap;
}
.punctuation {
}
a {
  color: #92b4f4;
}
a:link {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:active {
  text-decoration: none;
}
